import { useReducer } from "react";

const AuthenticationActionType = {
  LOGGING_IN: "LOGGING_IN",
  LOGIN_SUCCESS: "LOGIN_SUCCESS",
  LOGIN_ERROR: "LOGIN_ERROR",
  LOGGING_OUT: "LOGGING_OUT",
  LOGOUT_SUCCESS: "LOGOUT_SUCCESS",
};

function authenticationReducer(state, action) {
  switch (action.type) {
    case AuthenticationActionType.LOGGING_IN:
      return {
        isLoggedIn: false,
        isLoggingIn: true,
        isLoggingOut: false,
        error: null,
      };
    case AuthenticationActionType.LOGIN_SUCCESS:
      return {
        isLoggedIn: true,
        isLoggingIn: false,
        isLoggingOut: false,
        error: null,
      };
    case AuthenticationActionType.LOGIN_ERROR:
      const { error } = action;

      return {
        isLoggedIn: false,
        isLoggingIn: false,
        isLoggingOut: false,
        error: error,
      };
    case AuthenticationActionType.LOGGING_OUT:
      return {
        isLoggedIn: false,
        isLoggingIn: false,
        isLoggingOut: true,
        error: null,
      };
    case AuthenticationActionType.LOGOUT_SUCCESS:
      return {
        isLoggedIn: false,
        isLoggingIn: false,
        isLoggingOut: false,
        error: null,
      };
    default:
      break;
  }
}

function useAuthenticationReducer(initialState) {
  return useReducer(authenticationReducer, initialState);
}

export { AuthenticationActionType };

export default useAuthenticationReducer;
