export default class Game {
  constructor(world, cars, position) {
    this.world = world;
    this.cars = cars;
    this.position = position;
  }

  getCarPosition(carId) {
    const car = this.cars.find((c) => c.id === carId);

    if (!car) {
      return new Position(0, 0);
    }

    return new Position(car.x, car.y);
  }

  updateCar(car) {
    const existingCarIndex = this.cars.findIndex((c) => c.id == car.id);

    if (existingCarIndex !== -1) {
      this.cars.splice(existingCarIndex, 1);
    }

    this.cars.push(car);
  }

  draw(carsContext, terrainContext) {
    this.drawCars(carsContext);
    this.drawTerrain(terrainContext);
  }

  drawCars(carsContext) {
    const { width, height } = carsContext.canvas;

    carsContext.clearRect(0, 0, width, height);

    carsContext.save();

    carsContext.translate(
      width / 2 - this.position.x,
      height / 2 - this.position.y
    );
    this.cars.forEach((c) => c.draw(carsContext));

    carsContext.restore();
  }

  drawTerrain(terrainContext) {
    const { width, height } = terrainContext.canvas;

    terrainContext.clearRect(0, 0, width, height);

    terrainContext.save();

    terrainContext.translate(
      width / 2 - this.position.x,
      height / 2 - this.position.y
    );
    this.world.draw(terrainContext);

    terrainContext.restore();
  }
}
