import React, { createContext, useContext } from "react";
import useTokenStorage from "./use-token-storage";

const TokenStorageContext = createContext();

function useTokenStorageContext() {
  return useContext(TokenStorageContext);
}

function TokenStorageContextProvider({ children, tokenStorage }) {
  const tokenStorageValue = useTokenStorage(tokenStorage);

  return (
    <TokenStorageContext.Provider value={tokenStorageValue}>
      {children}
    </TokenStorageContext.Provider>
  );
}

export { useTokenStorageContext, TokenStorageContextProvider };
