export default class Car {
  constructor(id, x, y, width, height, rotation, carImage) {
    this.id = id;
    this.x = x;
    this.y = y;
    this.width = width;
    this.height = height;
    this.rotation = rotation;
    this.carImage = carImage;
  }

  draw(context) {
    context.save();

    context.translate(this.x, this.y);
    context.rotate(this.rotation * (Math.PI / 180));

    context.drawImage(
      this.carImage,
      -this.width / 2,
      -this.height / 2,
      this.width,
      this.height
    );

    context.restore();
  }
}
