import AccelerateCarHandler from "./cars/accelerate-car-handler";
import BrakeCarHandler from "./cars/brake-car-handler";
import SteerLeftCarHandler from "./cars/steer-left-car-handler";
import SteerRightCarHandler from "./cars/steer-right-car-handler";

export default class CarInputHandler {
  constructor(keyState, connection) {
    this.keyState = keyState;
    this.connection = connection;
  }

  async handleInput() {
    const handlers = this.getKeyHandlers();

    await Promise.all(handlers.map((handle) => handle()));
  }

  getKeyHandlers() {
    const controls = ["w", "a", "d", " "];

    return controls.map((c) => {
      const handler = this.getKeyHandler(c);

      if (this.keyState.isPressed(c)) {
        return handler.handlePressed.bind(handler);
      }

      return handler.handleUnpressed.bind(handler);
    });
  }

  getKeyHandler(key) {
    switch (key) {
      case "w":
        return new AccelerateCarHandler(this.connection);
      case "a":
        return new SteerLeftCarHandler(this.connection);
      case "d":
        return new SteerRightCarHandler(this.connection);
      case " ":
        return new BrakeCarHandler(this.connection);
      default:
        throw new Error("Key has no handler.");
    }
  }
}
