import Car from "../entities/car";
import Box from "../values/box";

export default class GameStateMapper {
  constructor(terrainFactory, carImage) {
    this.terrainFactory = terrainFactory;
    this.carImage = carImage;
  }

  mapToTerrainCollection(terrainCollectionState) {
    return terrainCollectionState.map((t) => this.mapToTerrain(t));
  }

  mapToTerrain(terrainState) {
    const { type, x, y, width } = terrainState;
    const box = new Box(x, y, width, width);

    return this.terrainFactory.createTerrain(box, type);
  }

  mapToCarsCollection(carsState) {
    return carsState.map((c) => this.mapToCar(c));
  }

  mapToCar(carState) {
    return new Car(
      carState.id,
      carState.position.x,
      carState.position.y,
      carState.width,
      carState.height,
      carState.rotation,
      this.carImage
    );
  }
}
