import { useCallback, useReducer } from "react";
import useAuthenticationReducer, {
  AuthenticationActionType,
} from "./use-authentication-reducer";

export default (authenticator) => {
  const initialAuthenticationState = {
    isLoggedIn: authenticator.isLoggedIn(),
    isLoggingIn: false,
    isLoggingOut: false,
    error: null,
  };

  const [authenticationState, dispatch] = useAuthenticationReducer(
    initialAuthenticationState
  );

  const googleLogin = useCallback(async (idToken) => {
    try {
      dispatch({ type: AuthenticationActionType.LOGGING_IN });

      await authenticator.googleLogin(idToken);

      dispatch({ type: AuthenticationActionType.LOGIN_SUCCESS });
    } catch (error) {
      dispatch({ type: AuthenticationActionType.LOGIN_ERROR, error });
    }
  });

  const logout = useCallback(() => {
    dispatch({ type: AuthenticationActionType.LOGGING_OUT });

    authenticator.logout();

    dispatch({ type: AuthenticationActionType.LOGOUT_SUCCESS });
  });

  return { authenticationState, googleLogin, logout };
};
