import { useEffect, useState } from "react";
import { HubConnectionBuilder } from "@microsoft/signalr";

function createGameHubConnection(gameHubUrl, tokenStorage) {
  return new HubConnectionBuilder()
    .withAutomaticReconnect()
    .withUrl(gameHubUrl, {
      accessTokenFactory: () => tokenStorage.getAccessToken(),
    })
    .build();
}

export default (gameHubUrl, tokenStorage) => {
  const [hubConnection, setHubConnectionState] = useState(() =>
    createGameHubConnection(gameHubUrl, tokenStorage)
  );

  useEffect(() => {
    return async () => {
      await hubConnection.stop();
    };
  }, []);

  return hubConnection;
};
