export default class InputKeyState {
  constructor() {
    this.keyState = {};

    document.addEventListener("keydown", (e) => {
      e.preventDefault();
      this.keyState[e.key.toLowerCase()] = true;
    });
    document.addEventListener(
      "keyup",
      (e) => (this.keyState[e.key.toLowerCase()] = false)
    );
  }

  isPressed(key) {
    return this.keyState[key];
  }
}
