import { useState } from "react";

import Game from "../../game/game";
import World from "../../game/worlds/world";
import Position from "../../game/values/position";

function createGame() {
  return new Game(new World([]), [], new Position(0, 0));
}

export default () => {
  const [game, setGame] = useState(() => createGame());
  const [position, setPositionState] = useState(() => new Position(0, 0));

  const setPosition = (updatedPosition) => {
    game.position = updatedPosition;
    setPositionState(updatedPosition);
  };

  const setTerrain = (terrain) => {
    game.world.terrain = terrain;
  };

  return { game, setTerrain, position, setPosition };
};
