import React from "react";

import "./header.scss";
import logo from "../../assets/images/logo.png";

import { useAuthenticationContext } from "../../hooks/authentication/authentication-context";

import PlayButton from "../play-button/play-button";
import AbsoluteLink from "../absolute-link/absolute-link";

export default () => {
  const { authenticationState, logout } = useAuthenticationContext();

  const getNavLinkProps = ({ isCurrent }) => {
    return {
      style: {
        opacity: isCurrent ? 1 : 0.6,
      },
    };
  };

  return (
    <header>
      <div className="container py-4">
        <div className="row align-items-center">
          <AbsoluteLink
            to="/"
            className="logo nav-action col-md-auto d-flex justify-content-center justify-content-md-end align-items-center"
          >
            <img className="header-logo" src={logo} alt="Logo" />
            <div className="ms-3">
              <strong className="title">Patron Lot</strong>
            </div>
          </AbsoluteLink>
          <div className="col-md mt-3 mt-md-0 nav-link-list">
            <div className="row align-items-center justify-content-center justify-content-md-end text-center">
              <AbsoluteLink
                className="nav-action col-sm-auto"
                to="/"
                getProps={getNavLinkProps}
              >
                Home
              </AbsoluteLink>

              {!authenticationState.isLoggedIn && (
                <AbsoluteLink
                  className="nav-action col-sm-auto mt-3 mt-sm-0 ms-sm-5"
                  to="/login"
                  getProps={getNavLinkProps}
                >
                  Login
                </AbsoluteLink>
              )}

              {authenticationState.isLoggedIn && (
                <div
                  className="nav-action col-sm-auto mt-3 mt-sm-0 ms-sm-5"
                  onClick={logout}
                >
                  Logout
                </div>
              )}

              <div className="col-sm-auto mt-3 mt-sm-0 ms-sm-5">
                <PlayButton />
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};
