import axios from "axios";

const baseGameServerUrl = process.env.GAME_SERVER_URL;

export default class CarProvider {
  async getUserCarId() {
    const response = await axios.get(`${baseGameServerUrl}/car`);

    if (!response.data.data) {
      throw new Error("Failed to get user car.");
    }

    const { id: userCarId } = response.data.data;

    return userCarId;
  }
}
