import "regenerator-runtime";

import React from "react";
import { Router, Link } from "@reach/router";
import axios from "axios";

import "popper.js";
import "bootstrap";

import "./app.scss";
import "bootstrap/dist/css/bootstrap.css";

import TokenStorage from "./services/token-storage";
import Authenticator from "./services/authenticator";
import createAuthInterceptor from "./services/interceptors/create-auth-interceptor";

import Home from "./pages/home";
import Contact from "./pages/contact";
import Play from "./pages/play";
import Login from "./pages/login";
import { AuthenticationContextProvider } from "./hooks/authentication/authentication-context";
import { TokenStorageContextProvider } from "./hooks/token-storage/token-storage-context";
import { BaseUrlContextProvider } from "./hooks/configuration/base-url-context";

const baseAuthenticationUrl = process.env.AUTHENTICATION_URL;
const baseGameServerUrl = process.env.GAME_SERVER_URL;
const baseUrl = process.env.BASE_URL;

const tokenStorage = new TokenStorage();
const authenticator = new Authenticator(tokenStorage, baseAuthenticationUrl);

axios.interceptors.request.use(createAuthInterceptor(tokenStorage));

export default () => (
  <BaseUrlContextProvider baseUrl={baseUrl}>
    <TokenStorageContextProvider tokenStorage={tokenStorage}>
      <AuthenticationContextProvider authenticator={authenticator}>
        <Router basepath={baseUrl}>
          <Home path="/" />
          <Contact path="/contact" />
          <Login path="/login" />
          <Play path="/play" />
        </Router>
      </AuthenticationContextProvider>
    </TokenStorageContextProvider>
  </BaseUrlContextProvider>
);
