import React, { useEffect, useState } from "react";
import GoogleLogin from "react-google-login";
import { navigate } from "@reach/router";
import Path from "path";

import { useAuthenticationContext } from "../hooks/authentication/authentication-context";

import Layout from "../components/layout/layout";
import { useBaseUrlContext } from "../hooks/configuration/base-url-context";

const googleClientId = process.env.GOOGLE_CLIENT_ID;

export default () => {
  const baseUrl = useBaseUrlContext();
  const [hasSubmittedLogin, setHasSubmittedLogin] = useState(false);
  const { authenticationState, googleLogin } = useAuthenticationContext();

  const onGoogleLoginSuccess = async ({ tokenId }) => {
    setHasSubmittedLogin(true);

    await googleLogin(tokenId);
  };

  useEffect(() => {
    if (hasSubmittedLogin && authenticationState.isLoggedIn) {
      navigate(baseUrl);
    }
  });

  return (
    <Layout>
      <div className="container text-center text-sm-start">
        <div className="page-header">Login</div>
        <div className="mt-4">Use a Google account to login to Patron Lot.</div>
        <div className="mt-4">
          <GoogleLogin
            clientId={googleClientId}
            onSuccess={onGoogleLoginSuccess}
          />
        </div>
      </div>
    </Layout>
  );
};
