import React from 'react';

import realTimeImage from '../../assets/images/real-time.png';
import multiplayerImage from '../../assets/images/multiplayer.png';
import persistentImage from '../../assets/images/persistent.png';

import FeatureCard from '../feature-card/feature-card';

export default () => {
  const features = [
    {
      title: 'Real-Time',
      description: 'See the world update',
      imageSource: realTimeImage,
    },
    {
      title: 'Multiplayer',
      description: 'Cruise with friends',
      imageSource: multiplayerImage,
    },
    {
      title: 'Persistent',
      description: 'Auto-saves world',
      imageSource: persistentImage,
    },
  ];

  const featuresElements = features.map((f) => (
    <div className="mb-4 mb-md-0 col-md-auto" key={f.title}>
      <FeatureCard title={f.title} description={f.description} imageSource={f.imageSource} />
    </div>
  ));

  return (
    <div>
      <div className="container py-5">
        <div className="row justify-content-between">{featuresElements}</div>
      </div>
    </div>
  );
};
