import React, { useEffect, useMemo, useRef, useState } from "react";
import { HubConnectionBuilder } from "@microsoft/signalr";

import "./game.scss";

import carImage from "../../assets/game-images/car.svg";
import grassImage from "../../assets/game-images/grass.png";
import asphaltImage from "../../assets/game-images/asphalt.png";

import InputKeyState from "../../game/input/input-key-state";
import CarInputHandler from "../../game/input/car-input-handler";
import SurroundingTerrainProvider from "../../game/services/surrounding-terrain-provider";
import GameUpdateListener from "../../game/listeners/game-update-listener";
import CarUpdateListener from "../../game/listeners/car-update-listener";
import TerrainFactory from "../../game/worlds/terrains/terrain-factory";
import Position from "../../game/values/position";
import GameStateMapper from "../../game/services/game-state-mapper";
import { useTokenStorageContext } from "../../hooks/token-storage/token-storage-context";
import useGameHubConnection from "../../hooks/game/use-game-hub-connection";
import useGame from "../../hooks/game/use-game";
import useCarProvider from "../../hooks/game/use-car-provider";

const baseGameServerUrl = process.env.GAME_SERVER_URL;
const hubUrl = `${baseGameServerUrl}/game`;

export default () => {
  const canvasContainerRef = useRef(null);
  const carsCanvasRef = useRef(null);
  const terrainCanvasRef = useRef(null);
  const carAssetRef = useRef(null);
  const grassAssetRef = useRef(null);
  const asphaltAssetRef = useRef(null);
  const [assetLoadedState, setAssetLoadedState] = useState({});

  const tokenStorage = useTokenStorageContext();
  const connection = useGameHubConnection(hubUrl, tokenStorage);
  const { game, position, setTerrain, setPosition } = useGame();
  const carProvider = useCarProvider();
  const [gameState, setGameState] = useState({
    carInputHandler: new CarInputHandler(new InputKeyState(), connection),
    surroundingTerrainProvider: new SurroundingTerrainProvider(connection),
    terrainFactory: new TerrainFactory(
      grassAssetRef.current,
      asphaltAssetRef.current
    ),
    gameUpdateListener: new GameUpdateListener(connection),
    carUpdateListener: new CarUpdateListener(connection),
  });

  const gameStateMapper = new GameStateMapper(
    gameState.terrainFactory,
    carAssetRef.current
  );

  // Move all this junk into some kind of state/custom hook.
  const sendSurroundingTerrain = async () => {
    const { width, height } = terrainCanvasRef.current;
    const maxDiameter = Math.max(width, height);
    const maxRadius = maxDiameter / 2;

    await gameState.surroundingTerrainProvider.sendSurroundingTerrain(
      game.position.x,
      game.position.y,
      maxRadius
    );
  };

  const updateGame = () => {
    gameState.carInputHandler.handleInput();
  };

  const updatePosition = (position) => {
    setPosition(position);

    sendSurroundingTerrain();
  };

  const updateCar = (carState) => {
    if (carState.id === game.userCarId) {
      updatePosition(carState.position);
    }

    const car = gameStateMapper.mapToCar(carState);
    game.updateCar(car);
  };

  const onCarAssetLoaded = () => {
    setAssetLoadedState((previous) => {
      return { ...previous, isCarAssetLoaded: true };
    });
  };

  const onGrassAssetLoaded = () => {
    setAssetLoadedState((previous) => {
      return { ...previous, isGrassAssetLoaded: true };
    });
  };

  const onAsphaltAssetLoaded = () => {
    setAssetLoadedState((previous) => {
      return { ...previous, isAsphaltAssetLoaded: true };
    });
  };

  const assetsLoaded = () => {
    return (
      assetLoadedState.isCarAssetLoaded &&
      assetLoadedState.isGrassAssetLoaded &&
      assetLoadedState.isAsphaltAssetLoaded
    );
  };

  const updateCanvasSize = () => {
    if (canvasContainerRef.current) {
      const {
        clientWidth: width,
        clientHeight: height,
      } = canvasContainerRef.current;

      carsCanvasRef.current.width = width;
      carsCanvasRef.current.height = height;

      terrainCanvasRef.current.width = width;
      terrainCanvasRef.current.height = height;

      sendSurroundingTerrain();
    }
  };

  const gameLoop = () => {
    const carsCtx = carsCanvasRef.current.getContext("2d");
    const terrainCtx = terrainCanvasRef.current.getContext("2d");

    game.draw(carsCtx, terrainCtx);

    requestAnimationFrame(gameLoop);
  };

  useEffect(async () => {
    if (assetsLoaded()) {
      gameState.carUpdateListener.onCarUpdate((car) => updateCar(car));
      gameState.gameUpdateListener.onUpdate(() => updateGame());
      gameState.surroundingTerrainProvider.onReceiveSurroundingTerrain(
        (terrainCollectionState) => {
          const terrain = gameStateMapper.mapToTerrainCollection(
            terrainCollectionState
          );

          setTerrain(terrain);
        }
      );

      await connection.start();

      game.userCarId = await carProvider.getUserCarId();

      updateCanvasSize();

      gameLoop();
    }
  }, [assetLoadedState]);

  useEffect(() => {
    window.addEventListener("resize", () => {
      updateCanvasSize();
    });
  }, [canvasContainerRef]);

  return (
    <div
      id="game"
      ref={canvasContainerRef}
      className="d-flex flex-column flex-grow-1"
    >
      <img
        ref={carAssetRef}
        className="asset"
        src={carImage}
        onLoad={onCarAssetLoaded}
      />
      <img
        ref={grassAssetRef}
        className="asset"
        src={grassImage}
        onLoad={onGrassAssetLoaded}
      />
      <img
        ref={asphaltAssetRef}
        className="asset"
        src={asphaltImage}
        onLoad={onAsphaltAssetLoaded}
      />

      <div id="canvasContainer" className="flex-grow-1">
        <canvas ref={carsCanvasRef} id="carsCanvas"></canvas>
        <canvas ref={terrainCanvasRef} id="terrainCanvas"></canvas>
      </div>

      <div className="game-ui">
        <div className="container py-3">
          <div>
            Position: {Math.round(position.x)}, {Math.round(position.y)}
          </div>
        </div>
      </div>
    </div>
  );
};
