export default class SurroundingTerrainProvider {
  constructor(connection) {
    this.connection = connection;
  }

  async sendSurroundingTerrain(x, y, range) {
    await this.connection.send("SendSurroundingTerrain", x, y, range);
  }

  onReceiveSurroundingTerrain(callback) {
    this.connection.on("ReceiveSurroundingTerrain", (terrain) =>
      callback(terrain)
    );
  }
}
