import axios from "axios";

export default class Authenticator {
  constructor(tokenStorage, baseAuthenticationUrl) {
    this.tokenStorage = tokenStorage;
    this.baseAuthenticationUrl = baseAuthenticationUrl;
  }

  isLoggedIn() {
    return this.tokenStorage.hasRefreshToken();
  }

  async googleLogin(idToken) {
    const googleLoginUrl = this.createGoogleLoginUrl(idToken);
    const response = await axios.post(googleLoginUrl);

    if (!response.data) {
      throw new Error(`Login failed. ${response.statusText}`);
    }

    const {
      accessToken,
      refreshToken,
      accessTokenExpirationTime,
    } = response.data.data;

    this.tokenStorage.setTokens(
      accessToken,
      refreshToken,
      accessTokenExpirationTime
    );
  }

  logout() {
    this.tokenStorage.clearTokens();
  }

  createGoogleLoginUrl(idToken) {
    return `${this.baseAuthenticationUrl}/google-login?idtoken=${idToken}`;
  }
}
