import Terrain from "./terrain";
import TerrainType from "./terrain-type";
import ColorTerrainDrawer from "../terrain-drawers/color-terrain-drawer";
import ImageTerrainDrawer from "../terrain-drawers/image-terrain-drawer";

export default class TerrainFactory {
  constructor(grassImage, asphaltImage) {
    this.grassImage = grassImage;
    this.asphaltImage = asphaltImage;
  }

  createTerrain(box, type) {
    return new Terrain(box, this.createTerrainDrawer(type));
  }

  createTerrainDrawer(type) {
    switch (type) {
      case TerrainType.GRASS:
        return this.createGrassTerrainDrawer();
      case TerrainType.ASPHALT:
      default:
        return this.createAsphaltTerrainDrawer();
    }
  }

  createGrassTerrainDrawer() {
    if (!this.grassImage) {
      return new ColorTerrainDrawer("#17ad49");
    }

    return new ImageTerrainDrawer(this.grassImage);
  }

  createAsphaltTerrainDrawer() {
    if (!this.asphaltImage) {
      return new ColorTerrainDrawer("#757575");
    }

    return new ImageTerrainDrawer(this.asphaltImage);
  }
}
