import React, { createContext, useContext } from "react";
import useAuthentication from "./use-authentication";

const AuthenticationContext = createContext();

function useAuthenticationContext() {
  return useContext(AuthenticationContext);
}

function AuthenticationContextProvider({ children, authenticator }) {
  const authentication = useAuthentication(authenticator);

  return (
    <AuthenticationContext.Provider value={authentication}>
      {children}
    </AuthenticationContext.Provider>
  );
}

export { useAuthenticationContext, AuthenticationContextProvider };
